import { Injectable } from '@angular/core';
import { HttpClient } from '@angular/common/http';
import { url } from '../url';

@Injectable({
  providedIn: 'root'
})
export class PostsService {

  constructor(
    private http:HttpClient
  ) { }


  getPostsByCat(catName){
    return this.http.get(`${url}api/getpostsbycat/${catName}`);
  }


  getPostById(pid){
    return this.http.get(`${url}api/getpostsbyid/${pid}`);
  }
}
