import { Injectable } from '@angular/core';
import { HttpClient } from '@angular/common/http';
import { url } from '../url';

@Injectable({
  providedIn: 'root'
})
export class CategoryService {

  constructor(
    private http:HttpClient
  ) { }

  getCat(){
    return this.http.get(`${url}api/getcategory`);
  }

  getCatData(cname){
    return this.http.get(`${url}api/fetchcat/${cname}`)
  }

  addContact(data){
    return this.http.post(`${url}api/contact`,data)
  }
}
