import { Component, OnInit } from '@angular/core';
import { ActivatedRoute } from '@angular/router';
import { CategoryService } from 'src/app/services/category.service';
import { PostsService } from 'src/app/services/posts.service';

@Component({
  selector: 'app-category',
  templateUrl: './category.component.html',
  styleUrls: ['./category.component.css']
})
export class CategoryComponent implements OnInit {

  getCat;
  getDetail;
  catData;
  postData;
  postDetails;
  constructor(
    private ar:ActivatedRoute,
    private catSer:CategoryService,
    private postSer:PostsService
  ) { 
    setInterval(()=>{
      this.getCat=this.ar.snapshot.paramMap.get('cat');
    },1000)
   }


  ngOnInit(): void {

  // get ar
  
  
  

  // get cat details
 setTimeout(()=>{
  this.catSer.getCatData(this.getCat).subscribe(res=>{
    this.catData=res;
    if(this.catData.err==0){
      this.getDetail=this.catData.msg; 
      console.log(this.getDetail);
      
    }
  })
 },1000)

  // get posts by cat

 setTimeout(()=>{
  this.postSer.getPostsByCat(this.getCat).subscribe(
    res =>{
      this.postData=res;
      if(this.postData.err==0){
        this.postDetails=this.postData.msg;
        console.log(this.postDetails)
      }
      
    }
  )
 },1000)
  
  }

}
